<script>
export default {
  name: 'Nav',
  props: {
    msg: String
  },
  data() {
    return {
      home: true,
      about: false,
      portfolio: false,
      contact: false
    }
  },
    computed : {
      homeActive()
      {
        return {
          active : this.home
        }
      },
      aboutActive()
      {
        return {
          active : this.about
        }
      },
      portfolioActive()
      {
        return {
          active : this.portfolio
        }
      },
      contactActive()
      {
        return {
          active : this.contact
        }
      },
    },
    methods: {
    showHome() {
      this.home = true
      this.about = false
      this.portfolio = false
      this.contact = false
      return this.$router.push('/');
      },
    showAbout() {
      this.home = false
      this.about = true
      this.portfolio = false
      this.contact = false
      return this.$router.push('/about');
      },
    showPortfolio() {
      this.home = false
      this.about = false
      this.portfolio = true
      this.contact = false
      return this.$router.push('/about');
      },
    showContact() {
      this.home = false
      this.about = false
      this.portfolio = false
      this.contact = true
      return this.$router.push('/about');
    }
  }
}
</script>

